import React, { Component } from 'react';
import "./navbar.css";

class Navbar extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            navMobileClass: '',
            navMobileClassBtn: 'btn',
            navMobileClassBG: ''
        };
    }
    toggleNavMobileClasses = ()=>{
        if(this.state.navMobileClass === 'nav-mob-anim-in')
        {
            this.setState({
                navMobileClass: 'nav-mob-anim-out',
                navMobileClassBtn: 'nav-mob-anim-out-btn btn',
                navMobileClassBG: 'nav-mob-anim-out-bg'
            });
        }
        else
        {
            this.setState({
                navMobileClass: 'nav-mob-anim-in',
                navMobileClassBtn: 'nav-mob-anim-in-btn btn',
                navMobileClassBG: 'nav-mob-anim-in-bg'
            });
        }
        
    }
    scrollMobile = (sender)=>{
        this.toggleNavMobileClasses();
        this.props.scrollTo(sender)
    }
    render() {
        return (
            <div>
                <div id="nav-mob-show" className={this.state.navMobileClass}>
                    <button aria-label="Show mobile menu" className={this.state.navMobileClassBtn} onClick={this.toggleNavMobileClasses}><i className="fas fa-ellipsis-h"></i></button>
                </div>
                <nav id="nav-mob" className={this.state.navMobileClass}>
                    <button aria-label="Scroll to introduction" onClick={this.scrollMobile} data-ref="Introduction" className="btn">Introduction</button>
                    <button aria-label="Scroll to education" onClick={this.scrollMobile} data-ref="Education" className="btn">Education</button>
                    <button aria-label="Scroll to skills" onClick={this.scrollMobile} data-ref="Skills" className="btn">Skills</button>
                    <button aria-label="Scroll to contact" onClick={this.scrollMobile} data-ref="Contact" className="btn">Contact</button>
                </nav>
                <div id="nav-mob-bg" className={this.state.navMobileClassBG} onClick={this.toggleNavMobileClasses}></div>
                <nav id="nav" style={{left: this.props.nav_offset}}>
                    <button aria-label="Scroll to introduction" onClick={this.props.scrollTo} data-ref="Introduction" className="btn">Introduction</button>
                    <button aria-label="Scroll to education" onClick={this.props.scrollTo} data-ref="Education" className="btn">Education</button>
                    <button aria-label="Scroll to skills" onClick={this.props.scrollTo} data-ref="Skills" className="btn">Skills</button>
                    <button aria-label="Scroll to contact" onClick={this.props.scrollTo} data-ref="Contact" className="btn">Contact</button>
                </nav>
            </div>
        );
    }
}

export default Navbar;



