import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import "./education.css";

class Education extends Component {
    render() {
        return (
            <div id="edu-card">
                <p className="card-title" >Education</p>
                <Timeline
                    scroll_pos={this.props.scroll_pos}
                    elements={this.props.elements} />
            </div>
        );
    }
}

class Timeline extends Component {
    constructor(props)
    {
        super(props);
        this.state = {elements: []};
    }
    componentDidMount = ()=>{
        var left = true;
        var elements = [];
        for (let i = 0; i < this.props.elements.length; i++) {
            elements.push(<TimelineElement
                key={i}
                scroll_pos={this.props.scroll_pos}
                direction={left === true ? 'timeline-left' : 'timeline-right'}
                icon={this.props.elements[i].icon}
                name={this.props.elements[i].name}
                date={this.props.elements[i].date}
                about={this.props.elements[i].about} />);
            left = !left;
        }
        this.setState({ elements: elements });
    }
    componentWillReceiveProps = (props)=>{
        if(this.state.elements.length)
        {
            var elements =[];
            this.state.elements.forEach(element => {
                elements.push(React.cloneElement(element, { scroll_pos: props.scroll_pos }));
            });
            this.setState({ elements: elements });
        }
    }
    render() {
        return (
            <div className="timeline-container">
                <div className="timeline-arrow"></div>
                <div className="timeline">
                    {this.state.elements}
                </div>
            </div>
        )
    }
}

class TimelineElement extends Component {
    checkScroll = (props)=>{
        var element = ReactDOM.findDOMNode(this.refs[props.name]);
        var top = element.getBoundingClientRect().top + window.pageYOffset || document.documentElement.scrollTop;
        if (props.scroll_pos + window.innerHeight >= top && !element.classList.contains("el-anim-in")) {
            
            element.classList = element.classList + " el-anim-in";
        }
    }
    componentDidMount = ()=>{
        this.checkScroll(this.props);
    }
    componentWillReceiveProps = (props)=>{
        this.checkScroll(props);
    }
    render() {
        return (
            <div className="el-wrapper">
                <div ref={this.props.name} className={"el-container " + this.props.direction}>
                    <div className="el-content">
                        <p className="el-name"><i className={"fas " + this.props.icon}></i> {this.props.name}</p>
                        <p className="el-date">{this.props.date}</p>
                        <p className="el-about">{this.props.about}</p>
                    </div>
                </div>
                <div className="el-circle"></div>
            </div>
        )
    }
}

export default Education;



