import React, { Component } from 'react';
import "./introduction.css";

class Introduction extends Component {
    constructor(props)
    {
        super(props);
        this.state = {age: null};
    }
    componentDidMount = ()=>{
        var birth = this.props.birth_date;
        var current = new Date();
        var age = new Date(current-birth);
        this.setState({age: age.getFullYear()-1970});
    }
    render() {
        return (
            <div>
                <div id="me-card">
                    <div id="me-photo">
                        <img src={this.props.profile_pic} alt="me"/>
                    </div>
                    <div id="me-contact-btn">
                        <button aria-label="Scroll to contact" className="btn" data-ref="Contact" onClick={this.props.scrollTo}><i className="fas fa-envelope"></i> Contact</button>
                    </div>
                    <div id="me-name">
                        <p>{this.props.name}</p>
                        <p>{this.props.subname}</p>
                    </div>
                    <div id="me-info-l">
                        <p className="me-section"><i className="fas fa-address-card"></i> Age</p>
                        <p>{this.state.age}</p>
                        <p className="me-section"><i className="fas fa-location-arrow"></i> Location</p>
                        <p>{this.props.location}</p>
                    </div>
                    <div id="me-info-r">
                        <p className="me-section"><i className="fab fa-linkedin"></i> LinkedIn</p>
                        <p><a href={this.props.linkedin_url}>{this.props.linkedin}</a></p>
                        <p className="me-section"><i className="fab fa-github"></i> GitHub</p>
                        <p><a href={this.props.github_url}>{this.props.github}</a></p>
                    </div>
                    <div id="me-about">
                        <p className="me-section"><i className="fas fa-user-circle"></i> About me</p>
                        <p>{this.props.about}</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Introduction;



