import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import "./skills.css";

class Skills extends Component {
    constructor(props) {
        super(props);
        this.state = { elements: [] }
    }
    updateSkills = (props) => {

    }
    componentDidMount = () => {
        var elements = [];
        for (let i = 0; i < this.props.skills.length; i++) {
            elements.push(<Skill
                key={i}
                scroll_pos={this.props.scroll_pos}
                name={this.props.skills[i].name}
                icon={this.props.skills[i].icon}
                level={this.props.skills[i].level}
                about={this.props.skills[i].about}
            />);
        }
        this.setState({ elements: elements })
    }
    componentWillReceiveProps = (props) => {
        if (this.state.elements.length) {
            var elements =[];
            this.state.elements.forEach(element => {
                elements.push(React.cloneElement(element, { scroll_pos: props.scroll_pos }));
            });
            this.setState({ elements: elements });
        }
    }
    render() {
        return (
            <div id="skills-card">
                <p className="card-title" >Skills</p>
                <div id="skills-container">
                    {this.state.elements}
                </div>
            </div>
        );
    }
}

class Skill extends Component {
    checkScroll = (props)=>{
        var grade = ReactDOM.findDOMNode(this.refs[props.name]);
        if (props.scroll_pos + window.innerHeight >= grade.offsetTop + 5 && !grade.classList.contains("skill-anim-in")) {
            grade.classList = grade.classList + " skill-anim-in";
        }
    }
    componentDidMount = ()=>{
        this.checkScroll(this.props);
    }
    componentWillReceiveProps = (props) => {
        this.checkScroll(props);
    }
    render() {
        return (
            <div className="skill">
                <p className="skill-name"><i className={this.props.icon}></i> <span style={{ color: "var(--primary-color)" }}>|</span> {this.props.name}</p>
                <div className="skill-grade-container">
                    <div ref={this.props.name} className="skill-grade" style={{ width: '100%' }}></div>
                </div>
                <p className="skill-about">{this.props.about}</p>
            </div>
        )
    }
}

export default Skills;



