import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import * as Scroll from 'react-scroll';
import "./App.css";
import Introduction from './introduction.js';
import Education from './education.js';
import Skills from './skills.js';
import Contact from './contact.js';
import Navbar from './navbar.js';
import bg from './img/bg.jpg';
import profile from './img/profile.jpg';
var scroll = Scroll.animateScroll;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navOffset: 0,
      scrollPos: 0
    }
  }
  scrollTo = (sender) => {
    var node;
    switch (sender.target.dataset.ref) {
      case "Introduction":
        node = this.refs.Introduction;
        break;
      case "Education":
        node = this.refs.Education;
        break;
      case "Skills":
        node = this.refs.Skills;
        break;
      case "Contact":
        node = this.refs.Contact;
        break;
      default:
        node = this.refs.Contact;
        break;
    }
    const card = ReactDOM.findDOMNode(node);
    scroll.scrollTo(card.offsetTop - 50, { duration: 500 });
  }
  componentDidMount = () => {
    window.onresize = () => {
      var app = document.getElementById('me-card');
      var appRight = app.getBoundingClientRect().right;
      this.setState({ navOffset: appRight });
    }
    window.onscroll = () => {
      this.setState({ scrollPos: window.scrollY });
    }
    window.onresize();
    window.onscroll();
  }
  render() {
    return (
      <div className="App">
        <div id="bg-photo" style={{ backgroundImage: "url('" + bg + "')" }}></div>
        <div id="content-bg"></div>
        <Navbar
          nav_offset={this.state.navOffset}
          scrollTo={this.scrollTo}
        />
        <Introduction
          ref="Introduction"
          scrollTo={this.scrollTo}
          name="John Smith"
          subname="My Guild Hall Smells of Rich Mahogany"
          profile_pic={profile}
          birth_date={new Date(1990, 4, 14, 0, 0, 0, 0)}
          location="Neverland, Pluto"
          linkedin="Lorem"
          linkedin_url=""
          github="Ipsum"
          github_url=""
          about="Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."
        />
        <Education
          ref="Education"
          scroll_pos={this.state.scrollPos}
          elements={
            [
              {
                name: "Pluto University of Technology",
                icon: "fa-arrow-right",
                date: "2016 - now",
                about: "Nulla imperdiet sit amet magna. Vestibulum dapibus, mauris nec malesuada fames ac turpis velit"
              },
              {
                name: "Secondary Technical School of turnip",
                icon: "fa-graduation-cap",
                date: "2012 - 2016",
                about: "Integer aliquam purus. Quisque lorem tortor fringilla sed"
              }
            ]
          } />
        <Skills
          ref="Skills"
          scroll_pos={this.state.scrollPos}
          skills={
            [
              {
                name: "Language 1",
                icon: "fas fa-cog",
                about: "vestibulum id, eleifend justo vel bibendum sapien massa ac turpis faucibus orci luctus non, consectetuer lobortis quis, varius in, purus. Integer ultrices posuere cubilia Curae"
              },
              {
                name: "Language 2",
                icon: "fas fa-calendar",
                about: "blandit eu, tempor diam pede cursus vitae, ultricies eu, faucibus quis, porttitor eros cursus lectus, pellentesque eget, bibendum a, gravida ullamcorper quam. Nullam viverra consectetuer. Quisque cursus "
              },
              {
                name: "Language 3",
                icon: "fas fa-address-book",
                about: "malesuada ultricies. Curabitur et ligula. Ut molestie a, ultricies porta urna. Vestibulum commodo volutpat a, convallis ac, laoreet enim. Phasellus fermentum"
              },
              {
                name: "Language 4",
                icon: "fas fa-align-left",
                about: "Nullam viverra consectetuer. Quisque cursus et, porttitor risus."
              },
              {
                name: "Something different",
                icon: "icon-reactjs",
                about: "Aliquam sem. In hendrerit nulla quam nunc, accumsan congue. Lorem ipsum primis "
              },
              {
                name: "Something cool",
                icon: "far fa-clone",
                about: "lacus, suscipit adipiscing. Cum sociis natoque penatibus et ultrices volutpat. Nullam wisi ultricies a, gravida vitae, dapibus risus ante sodales lectus blandit eu, tempor diam pede cursus vitae, ultricies eu, faucibus quis, porttitor eros cursus lectus, pellentesque eget, bibendum "
              },
              {
                name: "Language x",
                icon: "icon-html5-alt",
                about: "Lorem ipsum dolor sit amet enim. Etiam ullamcorper. Suspendisse a pellentesque dui, non felis. Maecenas malesuada elit lectus felis, malesuada ultricies. Curabitur et ligula. Ut molestie a, ultricies porta urna. Vestibulum commodo volutpat a, convallis ac"
              }
            ]
          } />
        <Contact
          ref="Contact"
        />
        <footer>
          <small>Copyright  2018 Tai-Min</small>
        </footer>
      </div>
    );
  }
}

export default App;



