import React, { Component } from 'react';
import "./contact.css";

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            overlay: undefined,
            err: undefined
        };
    }
    focus = (element) => {
        element.target.style.textAlign = 'left';
        element.target.placeholder = '';
        element.target.style.textIndent = '15px'
    }
    blur = (element) => {
        if (element.target.value === '') {
            element.target.style.textAlign = 'center';
            element.target.placeholder = element.target.dataset.placeholder;
            element.target.style.textIndent = '0px'
        }
    }
    sendMessage = (sender) => {
        var name = document.getElementById("contact-f-name").value;
        var email = document.getElementById("contact-f-email").value;
        var topic = document.getElementById("contact-f-topic").value;
        var message = document.getElementById("contact-f-message").value;
        if (name !== "" && email !== "" && topic !== "" && message !== "") {
            this.setState({ overlay: this.loading });
            setTimeout(() => {//replace this tiemout with post request
                this.setState({ overlay: this.sent });

                setTimeout(() => {
                    this.setState({ overlay: undefined, err: undefined });
                }, 5000);

            }, 1000);
            document.getElementById("contact-f-name").value = "";
            var r = {target : document.getElementById("contact-f-name")};
            this.blur(r);
            document.getElementById("contact-f-email").value = "";
            r = {target : document.getElementById("contact-f-email")};
            this.blur(r);
            document.getElementById("contact-f-topic").value = "";
            r = {target : document.getElementById("contact-f-topic")};
            this.blur(r);
            document.getElementById("contact-f-message").value = "";
            r = {target : document.getElementById("contact-f-message")};
            this.blur(r);
        }
        else {
            var errStr = "";
            if (name === "")
                errStr += "Name, ";
            if (email === "")
                errStr += "Email, ";
            if (topic === "")
                errStr += "Topic, ";
            if (message === "")
                errStr += "Message, ";
            errStr = "These fields are required: " + errStr;
            errStr = errStr.substring(0, errStr.length - 2);
            this.setState({ err: errStr });
        }

    }
    loading = (
        <div id="contact-overlay">
            <i className="fas fa-spinner fa-spin fa-4x fast-spin"></i>
        </div>
    );
    sent = (
        <div id="contact-overlay">
            <p>
            Your message has been  successfully sent.<br/>
            I will respond to you as fast as I can.
            </p>
        </div>
    );
    render() {
        return (
            <div id="contact-container">
                <div id="contact-card">
                    {this.state.overlay}
                    <p className="card-title">Contact</p>
                    <form id="contact-form">
                        <input id="contact-f-name" aria-label="Your name" required type="text" data-placeholder="Name*" onFocus={this.focus} onBlur={this.blur} style={{ gridArea: 'a' }} name="name" placeholder="Name*" />
                        <input id="contact-f-email" aria-label="Your email" required type="text" data-placeholder="Email*" onFocus={this.focus} onBlur={this.blur} style={{ gridArea: 'b' }} name="email" placeholder="Email*" />
                        <input id="contact-f-topic" aria-label="Topic of message" required type="text" data-placeholder="Topic*" onFocus={this.focus} onBlur={this.blur} style={{ gridArea: 'c' }} name="topic" placeholder="Topic*" />
                        <textarea id="contact-f-message" aria-label="Your message" required data-placeholder="Message*" onFocus={this.focus} onBlur={this.blur} name="message" placeholder="Message*"></textarea>
                        <p>{this.state.err} </p>
                        <button aria-label="Send message" type="button" onClick={this.sendMessage} className="btn" >Send</button>
                    </form>
                </div>
            </div>
        );
    }
}

export default Contact;



